import btnMixin from './btn-mixin'

const COMPONENT_NAME = 'cube-upload-btn'

export default {
  name: COMPONENT_NAME,
  mixins: [btnMixin],
  methods: {
    changeHandler(e) {
      const fileEle = e.currentTarget
      const files = fileEle.files
      if (files) {
        this.$parent.addFiles(files)
        fileEle.value = null
      }
    }
  }
}



