import { mapMutations, mapActions } from 'vuex'
import {
  SearchBox,
  LineBreak,
  SwitchesBox
} from 'components'

import { formatDate } from 'common/js/Util.js'
import { systemConfig } from 'common/js/config.js'

const debug = process.env.NODE_ENV !== 'production'
const DATATYPE = 'yyyy-MM-dd HH:mm:ss'
const SWICTHSUCCESS = ''

export default {
  name: 'changeproject',
  data () {
    return {
      currentIndex: 0,
      pullDownRefreshThreshold: 60,
      projects: [],
      EPS: [],
      focused: false,
      searchQuery: '',
      searchField: 'project_name',
      epsSize: 20,
      epsIndex: 0,
      projectSize: 10,
      projectIndex: 0
    }
  },
  computed: {
    offset () {
      return this.currentIndex === 0 ? 0 : -50
    },
    options () {
      return {
        pullDownRefresh: {
          threshold: parseInt(this.pullDownRefreshThreshold),
          txt: ''
        },
        pullUpLoad: {
          threshold: parseInt(this.pullDownRefreshThreshold),
          txt: ''
        },
        scrollbar: false
      }
    }
  },
  mounted () {
    // 
    this.projectPullingDown()
  },
  methods: {
    // 
    computedLink (item) {
      if (debug) {
        return '/static/img/projectDefault.png'
      } else {
        if (item.EpsProjIcon) {
          return `${systemConfig.imgBaseUrl}${item.EpsProjIcon}`
        } else {
          return '/weixin3.0/static/img/projectDefault.png'
        }
      }
    },
    // 
    getProjects (type = 'normal') {
      let obj = {
        type: 'proj',
        showClose: '0',
        keyword: this.searchQuery,
        index: this.projectIndex + '',
        size: this.projectSize + ''
      }

      this.getEpsProjectsData(obj).then((response) => {
        let data = response.data.value || []

        if (type === 'init') {
          this.projects = [...data]
        } else {
          this.projects = [...this.projects, ...data]
        }

        this.projectIndex += 1

        if (data.length === 0) {
          this.projectIndex -= 1
        }
      }).catch((e) => {
        this.AlertShowEvent(e.message)
      })
    },
    // 
    projectPullingDown () {
      this.projectIndex = 0
      this.getProjects('init')
    },
    // 
    projectPullingUp () {
      this.getProjects()
    },

    // EPS
    getEPS (type = 'normal') {
      let obj = {
        type: 'eps',
        showClose: '0',
        keyword: this.searchQuery,
        index: this.epsIndex + '',
        size: this.epsSize + ''
      }

      this.getEpsProjectsData(obj).then((response) => {
        let data = response.data.value || []

        if (type === 'init') {
          this.EPS = [...data]
        } else {
          this.EPS = [...this.EPS, ...data]
        }

        this.epsIndex += 1
        if (data.length === 0) {
          this.epsIndex -= 1
        }
      }).catch((e) => {
        this.AlertShowEvent(e.message)
      })
    },
    // eps
    epsPullingDown () {
      this.epsIndex = 0
      this.getEPS('init')
    },
    // eps
    epsPullingUp () {
      this.getEPS()
    },
    // 
    _switchEpsProject (item) {
      let ProjectGuid = item.project_guid
      this.SwitchEpsProjectData(ProjectGuid).then((response) => {
        this.ToastShowEvent(SWICTHSUCCESS)
        this.setProjectInfo(item)
        this.GetUserSession().then(() => {
          if (this.timer) {
            clearTimeout(this.timer)
          }
          this.timer = setTimeout(() => {
            this.$router.back()
            this.$emit('ReLoad')
          }, 1000)
        }).catch((e) => {
          this.AlertShowEvent(e.message)
        })
      }).catch((e) => {
        this.AlertShowEvent(e.message)
      })
    },
    // 
    searchChange (query) {
      this.searchQuery = query

      if (this.currentIndex === 0) {
        this.projectPullingDown()
      } else {
        this.epsPullingDown()
      }
    },
    // tab
    switchItem (index) {
      this.currentIndex = index
      if (this.currentIndex === 0) {
        if (!this.searchQuery) {
          if (this.projects.length === 0) {
            this.getProjects()
          }
        } else {
          this.projectPullingDown()
        }
      } else {
        if (!this.searchQuery) {
          if (this.EPS.length === 0) {
            this.getEPS()
          }
        } else {
          this.epsPullingDown()
        }
      }
    },
    _formatDate (time) {
      return formatDate(time, DATATYPE)
    },
    ...mapMutations({
      setProjectInfo: 'SET_PROJECTINFO'
    }),
    ...mapActions([
      'GetUserSession',
      'getEpsProjectsData',
      'SwitchEpsProjectData',
      'AlertShowEvent',
      'ToastShowEvent'
    ])
  },
  destroyed () {
    this.timer && clearTimeout(this.timer)
  },
  components: {
    SearchBox,
    SwitchesBox,
    LineBreak
  }
}



