
import { mapActions } from 'vuex'
import LineBreak from 'base/line/line.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'

import { ChangePass, PassRole } from 'api/index.js'
import { errLoginMixin } from 'common/js/mixin.js'

const NotEqual = ''

export default {
mixins: [errLoginMixin],
data () {
return {
title: '',
isLoading: false,
alertShow: false,
message: '',
alertTitle: '',
OldPass: '',
NewPass: '',
reNewPass: '',
toastShow: false,
deleyTime: 1000,
PassRoleObj: {},
placeholder: ''
}
},
mounted () {
this.PassRole(() => {
this.placeholder = this.checkPlaceholder()
})
},
methods: {
checkPlaceholder () {
let hasEnglish =
this.PassRoleObj.hasEnglish ? '' : ''
let hasNumber =
this.PassRoleObj.hasNumber ? ' ' : ''
let hasSymbol =
this.PassRoleObj.hasSymbol ? ' ' : ''
let minLength =
this.PassRoleObj.minLength ? ' ' + this.PassRoleObj.minLength + '' : ''

return `:${hasEnglish}${hasNumber} ${hasSymbol}${minLength}`
},
PassRole (callback) {
PassRole().then((response) => {
this.PassRoleObj = response.data
if (callback) {
callback()
}
}).catch((e) => {
this.errLogin(e)
})
},

changePass () {
if (this.NewPass === this.reNewPass) {
this._changePass()
} else {
this.message = NotEqual
this.alertShow = true
}
},

alertHideEvent () {
this.message = ''
this.alertShow = false
},

alertShowEvent (message) {
this.message = message
this.alertShow = true
},
back () {
this.$router.back()
},
_changePass () {
this.isLoading = true
ChangePass(this.OldPass, this.NewPass).then((response) => {
this.isLoading = false
if (!response.success) {
this.alertShowEvent(response.message)
} else {
this.toastShow = true
this._deleyClear()
}
}).catch((e) => {
this.isLoading = false
this.errLogin(e)
})
},
_deleyClear () {
if (this.timer) {
clearTimeout(this.timer)
}

this.timer = setTimeout(() => {
this.OldPass = ''
this.NewPass = ''
this.reNewPass = ''
}, this.deleyTime)
},
...mapActions([
'AlertShowEvent'
])
},
components: {
LineBreak,
Alert,
Loading,
Toast
}
}



