import { systemConfig } from 'common/js/config.js'
import { getUserSession, storeUserSession } from 'api/UserSession'
import Loading from 'vux/src/components/loading/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Group from 'vux/src/components/group/index.vue'

import { DatePicker } from 'cube-ui'
import { formatDate, clearStorage } from 'common/js/Util.js'
import { mapMutations } from 'vuex'

import {
NavList,
SwitchesBox,
PowerSelect,
FileAttach,
Report,
Comment,
SearchBox,
FormList,
FooterBar
} from 'components/index.js'

export function getHeaderAvatar (HumanHeadId) {
if (HumanHeadId) {
return systemConfig.devDomainName + systemConfig.imgBaseUrl + HumanHeadId
} else {
return './default-sir.jpg'
}
}


export const commonComponentMixin = {
data () {
return {
mx_isLoading: false,
mx_message: '',
mx_alertShow: false,
mx_alertTitle: '',
mx_toastShow: false,
mx_deleyTime: 1000
}
},
methods: {

MinXinHttpFetch (httpPromise, callback, isAdmin) {
this.mx_isLoading = true
httpPromise.then((response) => {
this.mx_isLoading = false
if (response.success) {
callback(response)
this.MixinAlertHideEvent()
} else {
callback(response)
if (!isAdmin) {
this.MixinAlertShowEvent(response.message)
}
}
}).catch((err) => {
this.mx_isLoading = false
this.MixinAlertShowEvent(err.message)
})
},

MixinAlertShowEvent (message) {
this.mx_message = message
this.mx_alertShow = true
},

MixinAlertHideEvent () {
this.mx_message = ''
this.mx_alertShow = false
},
_getAvatar () {
if (this.UserSession && this.UserSession.HumanHeadSmall) {
this.UserSession.avatar = getHeaderAvatar(this.UserSession.HumanHeadSmall)
} else {
return false
}
},
_UserSession (callback) {
this.MinXinHttpFetch(getUserSession(), (response) => {
storeUserSession(response.data.value)
if (callback) {
callback(response)
}
})
},
beforeDestroy () {
if (this.timer) {
clearTimeout(this.timer)
}
}
},
components: {
Loading,
Alert,
Toast
}
}


export const formComponentMixin = {
methods: {

_formatDate (date) {
return formatDate(date)
}
},
components: {
NavList,
SwitchesBox,
PowerSelect,
FileAttach,
Report,
Comment,
SearchBox,
FormList,
FooterBar,
XTextarea,
Group,
DatePicker
}
}

export const commonMixin = {
components: {
Loading,
Alert,
Toast
}
}

export const errLoginMixin = {
methods: {
errLogin (e) {
if (e.message === '201') {
clearStorage()
this.SetTokenError(true)
this.$router.push('/login')
} else {
this.AlertShowEvent(e.message)
}
},
...mapMutations({
'SetTokenError': 'TOKENERR'
})
}
}



