import { mapGetters, mapActions, mapMutations } from 'vuex'
import { getTokenString } from 'api/login.js'

const debug = process.env.NODE_ENV !== 'production'

export default {
  data () {
    return {
      Name: '',
      PassWord: '',
      mx_message: '',
      mx_alertShow: false,
      mx_alertTitle: '',
      mx_toastShow: false,
      mx_deleyTime: 1000
    }
  },
  created () {
    this.keydownEvent()
  },
  computed: {
    ...mapGetters([
      'tokenErr'
    ])
  },
  mounted () {
    let Token = getTokenString()

    if (Token) {
      this.$router.push('/business')
    }
  },
  methods: {
    // 
    unbindKeyDown () {
      document.removeEventListener('keydown', this.bindKeyDown)
    },
    // 
    bindKeyDown (e) {
      var keyCode = e.keyCode
      if (keyCode === 13) {
        if (!this.Name) {
          return false
        }
        this.login()
      }
    },
    // enter 
    keydownEvent () {
      document.addEventListener('keydown', this.bindKeyDown)
    },
    // 
    login () {
      // 
      this.LoginIN({
        Name: this.Name,
        PassWord: this.PassWord
      }).then(() => {
        this.SetTokenError(false)
        // UserSession
        this.GetUserSession().then((res) => {
          let { HumanId } = res
          this.UserSession = Object.assign({}, res)

          if (debug) {
            this.$router.push('/business')
          } else {
            window.open('/weixin3.0/Reg.ashx?hum=' + HumanId, '_self')
          }
        }).catch((e) => {
          this.AlertShowEvent(e.message)
        })
      }).catch((e) => {
        this.AlertShowEvent(e.message)
      })
    },
    ...mapActions([
      'LoginIN',
      'GetUserSession',
      'AlertShowEvent'
    ]),
    ...mapMutations({
      'SetTokenError': 'TOKENERR'
    })
  },
  destroyed () {
    this.unbindKeyDown()
  }
}



