
import CheckIcon from 'vux/src/components/check-icon/index.vue'

import { localStorageTag } from 'common/js/config'
import storage from 'good-storage'
import { getTableStyleIndex } from 'common/js/Util.js'

export default {
data () {
return {
currentIndex: 0,
tableStyles: [{
title: '',
style: 'default'
}, {
title: '',
style: 'contractStyle'
}, {
title: '',
style: 'beauty'
}, {
title: 'Table',
style: 'table'
}, {
title: '',
style: 'luxury'
}, {
title: '',
style: 'luxuryBeauty'
}]
}
},
mounted () {
this.getIndex()
},
methods: {
selectedItem (index, item) {
this.currentIndex = index
this.storageStyle(item)
},
storageStyle (item) {
storage.set(localStorageTag + 'tableStyle', item.style)
},
getIndex () {
let style = storage.get(localStorageTag + 'tableStyle')
this.currentIndex = getTableStyleIndex(style, this.tableStyles)
}
},
components: {
CheckIcon
}
}



