
import Alert from 'vux/src/components/alert/index.vue'

import { storageToken } from 'api/login'
import { mapActions } from 'vuex'

const ERROR = ''

export default {
data () {
return {
alertShow: false,
alertTitle: '',
message: '',
token: ''
}
},
created () {
this.goToPage()
},
methods: {

goToPage () {
let { query } = this.$router.history.current
let { url, token } = query

if (url) {
this.$router.push(url)
}

if (token) {
this.token = token
storageToken(token)
this.getUserSession()
}

if (!url && !token) {
this.ShowHideEvent(ERROR)
}
},
getUserSession () {

this.GetUserSession().then((res) => {
this.$router.push('/business')
}).catch((e) => {
this.AlertShowEvent(e.message)
})
},

AlertHideEvent () {
this.alertShow = false
},

ShowHideEvent (msg) {
this.message = msg
this.alertShow = true
},
...mapActions([
'GetUserSession',
'AlertShowEvent'
])
},
components: {
Alert
}
}



