import { mapMutations, mapActions } from 'vuex'
import { SwitchesBox, SearchBox, backBtn } from 'components'
import { findIndex, formatDate } from 'common/js/Util.js'
import { errLoginMixin } from 'common/js/mixin.js'

export default {
  mixins: [errLoginMixin],
  name: 'workflow',
  data () {
    return {
      searchQuery: '',
      ready: false,
      notifyInfos: {},
      selectedLabel: '',
      tabLabels: [
        {
          label: ''
        },
        {
          label: ''
        },
        {
          label: ''
        }
      ],
      messageHeight: 200,
      slideOptions: {
        listenScroll: true,
        probeType: 3,
        directionLockThreshold: 0
      },
      scrollOptions: {
        directionLockThreshold: 0,
        pullDownRefresh: {
          threshold: 60,
          txt: ''
        },
        pullUpLoad: true
      },
      Infos: [],
      Actived: [],
      Join: [],
      currentPage: {
        Infos: 0,
        Actived: 0,
        Join: 0
      }
    }
  },
  created () {
    this.checkHeight()

    window.addEventListener('resize', () => {
      this.checkHeight()
    })
  },
  computed: {
    initialIndex () {
      let index = 0
      index = findIndex(this.tabLabels, item => item.label === this.selectedLabel)
      return index
    }
  },
  mounted () {
    // 
    let that = this
    this._MyNotifyInfos({
      success (data) {
        that.Infos = [...data.infos]
        that.Actived = [...data.actived]
        that.Join = [...data.join]
      }
    })
  },
  methods: {
    // 
    onPullingDown (type) {
      let that = this
      that.currentPage[type] = 0
      that._MyNotifyInfos({
        type,
        success (data) {
          that[type] = [...data[type.toLowerCase()]]
          that.$refs[type].forceUpdate()
        }
      })
    },
    // 
    onPullingUp (type) {
      let that = this
      that.currentPage[type] += 1
      that._MyNotifyInfos({
        type,
        index: this.currentPage[type],
        success (data) {
          let newData = [...data[type.toLowerCase()]]

          if (newData.length === 0) {
            that.currentPage[type] -= 1
          }

          that[type] = that[type].concat(newData)
          that.$refs[type].forceUpdate()
        }
      })
    },
    // 
    searchFunc (query, target) {
      this.searchQuery = query

      let that = this
      let search = true
      let type = target

      that.currentPage[type] = 0
      if (query === '' || !query) {
        search = false
      }

      that._MyNotifyInfos({
        type,
        search: search,
        success (data) {
          that[type] = [...data[type.toLowerCase()]]
          that.$refs[type].forceUpdate()
        }
      })
    },
    // 
    openFrom (item) {
      if (!item.KeyValue || !item.KeyWord) {
        this.setCurrentMessage(item)
        this.$router.push(`/showMessage/${item.Id}`)
      } else {
        let url = `/weixinform/${item.HtmlPath}/view/${item.KeyValue}`

        if (item.IsPowerMessage === 3) {
          this.$router.push(url)
        } else {
          this.HasReadMessage(item).then(() => {
            this.GetInformCount().then(() => {
              this.$router.push(url)
            }).catch((e) => {
              this.errLogin(e)
            })
          }).catch((e) => {
            this.errLogin(e)
          })
        }
      }
    },
    // 
    _MyNotifyInfos (option) {
      let types = this.getTypes(option.type)
      let params = Object.assign({}, {
        types: types,
        index: option.index || 0,
        size: option.size || 8,
        swhere: '1=1',
        humanid: ''
      })

      if (option.search) {
        params.swhere += ` and (Title like '%${this.searchQuery}%' or EpsProjName like '%${this.searchQuery}%')`
      }

      this.MyNotifyInfosData(params).then((res) => {
        this.ready = true
        if (option.success) {
          option.success(res.data)
        }
      }).catch((e) => {
        this.ready = false
        this.errLogin(e)
      })
    },
    // 
    getTypes (type) {
      let types = 'infos,actived,join'

      if (type === 'Infos') {
        types = 'infos'
      } else if (type === 'Actived') {
        types = 'actived'
      } else if (type === 'Join') {
        types = 'join'
      }

      return types
    },
    // 
    _FormatDate (date) {
      return formatDate(date, 'yyyy-MM-dd HH:mm:ss')
    },
    changePage (current) {
      this.selectedLabel = this.tabLabels[current].label
    },
    scroll (pos) {
      const x = Math.abs(pos.x)
      const tabItemWidth = this.$refs.tabNav.$el.clientWidth
      const slideScrollerWidth = this.$refs.slide.slide.scrollerWidth
      const deltaX = x / slideScrollerWidth * tabItemWidth
      this.$refs.tabNav.setSliderTransform(deltaX)
    },
    checkHeight () {
      this.$nextTick(() => {
        this.messageHeight =
        this.$refs.messageBox.offsetHeight -
        this.$refs.messageHeader.offsetHeight
      })
    },
    isShow (num) {
      return num === this.currentIndex
    },
    switchItem (index) {
      this.currentIndex = index
    },
    ...mapMutations({
      setCurrentMessage: 'SET_CURRENTMESSAGE'
    }),
    ...mapActions([
      'MyNotifyInfosData',
      'HasReadMessage',
      'GetInformCount',
      'AlertShowEvent'
    ])
  },
  components: {
    SwitchesBox,
    SearchBox,
    backBtn
  }
}



