import { mapGetters, mapActions } from 'vuex'
import { commonMixin } from 'common/js/mixin.js'
import TabBar from 'base/TabBar/TabBar.vue'

export default {
  name: 'App',
  mixins: [commonMixin],
  computed: {
    AlertShow () {
      return Object.assign({}, this.Alert)
    },
    ToastShow () {
      return Object.assign({}, this.Toast)
    },
    ...mapGetters([
      'showLoading',
      'Alert',
      'Toast'
    ])
  },
  methods: {
    ...mapActions([
      'AlertHideEvent'
    ])
  },
  components: {
    TabBar
  }
}



