import { mapGetters } from 'vuex'
import { getStoreUserSession } from 'api/UserSession.js'

export default {
  computed: {
    UserSession () {
      return getStoreUserSession()
    },
    ...mapGetters([
      'UserInfo'
    ])
  },
  methods: {
    openEditBlock (field) {
      this.$router.push({
        path: '/peopleCenter/Setting/updateinfo',
        query: {
          field: field
        }
      })
    }
  }
}



